<template>
    <div id="communityVue" style="height: 100%;">
        <layoutVue
            ref="layoutVue">
            <template v-slot:body>
                커뮤니티
            </template>
        </layoutVue>
    </div>
</template>

<script>
import layoutVue from "@/components/Layout.vue"
export default {
    name: 'communityVue',
    components: {
        layoutVue
    },
    data() {
        return {
            socket: null,   
        }
    },
    methods: {
        load_cmu_list: function() {
            let vm = this;
            vm.socket.emit('/socket/community', {}, (data, err) => {
                console.log("data:", data);
            });
        },
    },
    mounted: function() {
        let vm = this;
        if(!vm.$store.state.user.socketId) {
            vm.$router.push('/login');
        }
        vm.socket = vm.$store.state.socket;

        vm.load_cmu_list();
    },
}
</script>