<template>
    <div id="homeVue" style="height: 100%; overflow-y: scroll;">
        <layoutVue 
            ref="layoutVue">
            <template v-slot:body>
                <van-notice-bar text="공지사항입니다."
                    left-icon="volume-o" ></van-notice-bar>
                <div style="width: 100%;">
                    <div class="text-center">
                        다음일정
                        <van-count-down :time="time">
                            <template #default="timeData">
                                <span style="background-color: blue; color: white;">{{ timeData.hours }}</span>
                                <span class="colon">:</span>
                                <span class="block">{{ timeData.minutes }}</span>
                                <span class="colon">:</span>
                                <span class="block">{{ timeData.seconds }}</span>
                            </template>
                        </van-count-down>
                    </div>
                    인기게시물
                    <van-skeleton>
                        <template #template>
                            <div style="display: flex; width: 100%;">
                                <van-skeleton-image />
                                <div :style="{ flex: 1, marginLeft: '16px' }">
                                    <van-skeleton-paragraph row-width="60%" />
                                    <van-skeleton-paragraph />
                                    <van-skeleton-paragraph />
                                    <van-skeleton-paragraph />
                                </div>
                            </div>
                        </template>
                    </van-skeleton>
                </div>
            </template>
        </layoutVue>
    </div>
</template>

<script>
import layoutVue from "@/components/Layout.vue"

export default {
    name: 'homeVue',
    components: {
        layoutVue
    },
    data() {
        return {
            time: (30 * 60 * 60 * 1000),
        }
    },
    methods: {
        
    },
    mounted: function() {
        let vm = this;
        if(!vm.$store.state.user.socketId) {
            vm.$router.push('/login');
        }
    },
}
</script>